<template>
  <van-popup v-model="visible" position="bottom" :overlay="true">
	  <van-picker show-toolbar  title="选择城市"
	   :columns="Category" @confirm="onConfirm" @cancel="clear"
	   value-key="Category"
	    @change="onChange"
		cancel-button-text="清除选择"/>
  </van-popup>
</template>

<script>
import { picker, Popup } from "vant";
import {getProvince,getCity,getArea} from "@/api/v4.js"
export default {
  name: "about",
  components: {
    [picker.name]: picker,
    [Popup.name]: Popup
  },
  props: {
    confirm: Function,
    value: String
  },
  data() {
    return {
	 Category:[],
      columns:[],
      visible: false,
	  itemArr:[],
	  itemArr2:[],
	  itemArr3:[],
	   columns2:[],
	   columns3:[],
    };
  },
  created() {
  	getProvince().then(res=>{
		this.columns = res;
		 let itemArrId = [];
		this.columns.map(item=>{
			this.itemArr.push(item.name)
			itemArrId.push(item.id)
		})
		getCity({province_id:itemArrId[0]}).then(city=>{
			let itemArrId2 = [];
			this.columns2 = city
			city.map(i=>{
				this.itemArr2.push(i.name)
				itemArrId2.push(i.id)
			})
			getArea({city_id:itemArrId2[0],province_id:itemArrId[0]}).then(Area=>{
				this.columns3 = Area
				let itemArrId3 = [];
				Area.map(isp=>{
					this.itemArr3.push(isp.name)
					itemArrId3.push(isp.id)
				})
			})
		})
		this.Category=[
		  {
		    values: this.itemArr,
		    className: 'column1',
		  },
		  {
		    values: this.itemArr2,
		    className: 'column2',
		  },
		  {
		    values: this.itemArr3,
		    className: 'column3',
		  },
		]
	})
  },
  methods: {
    show() {
      this.visible = true;
    },
    hide() {
      this.visible = false;
    },
    onConfirm(list,index) {
		console.log(this.columns3[index[2]])
	  const id = [this.columns[index[0]].id,this.columns2[index[1]].id,this.columns3[index[2]].id]
      this.$emit("confirm",list,id);
      this.hide();
    },
    clear() {
      this.$emit("confirm", "");
      this.hide();
    },
	onChange(picker, values, index){
		this.columns.map((i,v)=>{
			if(i.name == values[0]){
				getCity({province_id:i.id}).then(res=>{
					this.columns2 = res
					let newarr = [];
					let newId = [];
					res.map(item=>{
						newarr.push(item.name)
						newId.push(item.id)
					})
					picker.setColumnValues(1, newarr)
					if(newarr){
						let io = picker.getIndexes(index)
						console.log(io[1])
						getArea({city_id:newId[io[1]],province_id:i.id}).then(Area=>{
							console.log(Area)
							this.columns3 = Area
							let newarr2 = [];
							Area.map(item2=>{
								newarr2.push(item2.name)
							})
							picker.setColumnValues(2, newarr2)
						})
					}
				})
			}
		})
	}
  }
};
</script>