<template>
	<div class="worksop">
		<van-nav-bar title="空间严选"  fixed border  @click-left="goBack" left-text left-arrow>
		  <template #right>
		    <van-icon @click="home" name="wap-home" size="20" color="#c40024"/>
		  </template>
		</van-nav-bar>
		<div class="bg">
			<van-row type="flex" justify="space-between" class="celbox">
				<van-col span="7" @click="showAreaPopup" class="sleck van-ellipsis">
					{{ selectedAreaName || "选择地区" }}
					<i class="iconfont iconxia"></i>
				</van-col>
				<van-col span="7" class="sleck van-ellipsis" @click="showMeasurePopup">{{selectMeasureName || "选择面积"}} <i class="iconfont iconxia"></i></van-col>
				<van-col span="7" class="sleck van-ellipsis" @click="showPricePopup">{{selectPriceName || '区间价格'}} <i class="iconfont iconxia"></i></van-col>
			</van-row>
			<div class="yyjx_row">
				<van-checkbox-group v-model="result" direction="horizontal" max="3" @change="hdchange">
					<van-checkbox :name="index" v-for="(item,index) in arr" :key="index" class="yyjx_item" @click="clickCheck(item,index,item.title)"
					 :class="{active:currIndex.indexOf(index)!=-1}">
						<span :class="{active2:currIndex.indexOf(index)!=-1}">{{item.title}}</span>
					</van-checkbox>
				</van-checkbox-group>
				<!-- <div class="yyjx_item" v-for="(item,index) in arr.tags" :key="index" @click="clickCheck(item,index,item.title)" :class="{active:chooseAfterValue.indexOf( item.title )!=-1}">
					<span :class="{active2:chooseAfterValue.indexOf( item.title )!=-1}">{{item.title}}</span>
				</div> -->
			</div>
		</div>
		<van-list v-model="isUpLoading" :finished="upFinished" :immediate-check="false" :offset="10" finished-text="－暂无更多内容－"
		 @load="onLoadList">
			<div class="worksop_centent">
				<div class="worksop_centent_box" @click="gotoDetails(item.house_id)" v-for="(item,index) in indexArr" :key="index">
					<van-image width="32vw" height="23vw" :src="item.cover" fit="cover" >
					<template v-slot:error><img  width="100%" height="100%" src="http://demo.oramage.com/demo/tianji-vue-v3/img/loding.jpg" alt=""></template>
					<template v-slot:loading><img  width="100%" height="100%" src="http://demo.oramage.com/demo/tianji-vue-v3/img/loding.jpg" alt=""></template>
					</van-image>
					<div class="worksop_info">
						<p class="van-ellipsis">{{item.title}}</p>
						<p><i class="iconfont iconbiaodiandidian"></i>{{item.area}} </p>
						<p><span class="cor"><b>{{item.rent}}</b>元 / m² </span>︱<span>{{item.rental_area}}m²</span></p>
					</div>
				</div>
			</div>
			<div class="no-content" v-if="indexArr.length == 0">
			  <img src="../../assets/no-content.png" alt="" />
			  <p><strong>－暂无更多内容－</strong></p>
			</div>
		</van-list>
		<van-button type="warning" size="large" class="fix" block @click="clickWt">委托找房</van-button>
		<van-popup v-model="show" lock-scroll class="wdit">
			<van-cell-group class="models">
				<div class="cf_bg">
					<p>天集社<br />委托找房登记</p>
				</div>
				<van-cell @click="showProvince" title="厂房位置" :value="selectAreaVal?selectAreaVal:'请选择'" size="large" is-link
				 required />
				<van-cell @click="showAddMeasure" title="厂房面积" :value="selectMeasureName2?selectMeasureName2:'请选择'" size="large"
				 is-link />
				<van-cell @click="showAddPrice" title="资金预算" :value="selectPriceName2?selectPriceName2:'请选择'" size="large" is-link />
				<van-cell title="其它要求" :border="false" />
				<van-field class="textarea" v-model="remark" type="textarea" :border="true" placeholder="请描述你的要求，如：找一个靠近高速带装修的厂房"
				 clearable />
				<p class="popu_ps">请尽量完善您的找房需求，以便为您找到更合适的房源</p>
				<van-row type="flex" justify="space-between" class="but">
					<van-col span="12">
						<div class="buts" @click="show = false">取消</div>
					</van-col>
					<van-col span="12">
						<div class="buts colors" @click="saveInfo">提交</div>
					</van-col>
				</van-row>
			</van-cell-group>
		</van-popup>
		<memberRegion ref="areaPopup" :value="selectedAreaCode" @confirm="selectArea" />
		<measurePopup ref="measurePopup" @confirm="selectMeasure" />
		<provincePopup ref="provincePopups" @confirm="seleProvince" />
		<pricePopup ref="pricePopup" @confirm="selectPrice" />
		<addmeasure ref="addmeasure" @confirm="selectmeasure" />
		<addPrice ref="addPrice" @confirm="selectaddPrice" />
	</div>
</template>

<script>
	import {
		Col,
		Row,
		NavBar,
		Cell,
		CellGroup,
		Button,
		Field,
		Picker,
		Checkbox,
		CheckboxGroup,
		Popup,
		PullRefresh,
		List,
		Image
	} from "vant";
	import memberRegion from "@/components/memberRegion"
	import measurePopup from "@/components/measure-popup"
	import provincePopup from "@/components/province-popup"
	import pricePopup from "@/components/price-popup"
	import addmeasure from "@/components/addmeasure"
	import addPrice from "@/components/addPrice"
	import {
		mapActions,
		mapGetters
	} from "vuex";
	import {
		houseIndex,
		getByHouse,
		findHouseAdd
	} from "@/api/v4.js"
	export default {
		components: {
			[NavBar.name]: NavBar,
			[Col.name]: Col,
			[Row.name]: Row,
			[Cell.name]: Cell,
			[CellGroup.name]: CellGroup,
			[Button.name]: Button,
			[Field.name]: Field,
			[Picker.name]: Picker,
			[Checkbox.name]: Checkbox,
			[CheckboxGroup.name]: CheckboxGroup,
			[PullRefresh.name]: PullRefresh,
			[List.name]: List,
			[Popup.name]: Popup,
			[Image.name]: Image,
			memberRegion,
			measurePopup,
			pricePopup,
			provincePopup,
			addmeasure,
			addPrice
		},
		data() {
			return {
				show: false,
				isShow: false,
				currIndex: [],
				selectedArea: [],
				selectedProvince: [],
				molselectedArea: [],
				selectMeasureArr: [],
				selectMeasureArr2: [],
				selectPriceArr: [],
				selectPriceArr2: [],
				result: [],
				indexArr: [],
				arr: [],
				nullTip: '空空如也~',
				pageSize: 20,
				pageIndex: 1,
				isDownLoading: false,
				isUpLoading: false,
				upFinished: false,
				offset: 100,
				province_id: null,
				city_id: null,
				area_id: null,
				area: null,
				budget: null,
				remark: null,
				rental_area: null,
				rent: null,
				items: [],
				types: [],
				changeitems: [],
				chooseAfterValue: [],
				floor :null,
				structure :null,
				clickIndex:[]
			}
		},
		created() {
			this.getData()
		},
		watch: {
			// indexArr(val, newval) {
			//    this.indexArr = val
			// }
		},
		methods: {
			goBack() {
				this.$router.back();
			},
			home(){
				this.$router.push("/")
			},
			clickWt(){
				if(this.userInfo.status == 2){
					this.show = true
				}else{
					this.$dialog
					  .confirm({
					    title: "温馨提示",
					    message: "您需要注册后才能进行该操作哦",
					    confirmButtonText: "马上注册",
					    cancelButtonText: "稍后再说"
					  })
					  .then(() => {
						  this.$router.push("/my-card");
					  })
					  .catch(() => {
					    // on cancel
					    console.log("cancel dialog");
					  });
				}
			},
			selectArea(list, id) {
				if (list || id) {
					this.selectedArea = list;
					this.province_id = id[0];
					this.city_id = id[1];
					this.area_id = id[2];
					this.getData()
				} else {
					this.selectedArea = []
					this.province_id = null;
					this.city_id = null;
					this.area_id = null;
					this.getData()
				}

			},
			showAreaPopup() {
				this.$refs["areaPopup"].show();
				this.isShow = this.$refs["areaPopup"].visible;
			},
			showPricePopup() {
				this.$refs["pricePopup"].show();
			},
			showAddMeasure() {
				this.$refs["addmeasure"].show();
			},
			showProvince() {
				this.$refs["provincePopups"].show();
			},
			showAddPrice() {
				this.$refs["addPrice"].show();
			},
			clickCheck(item, index, title) {
				// if(this.currIndex.length >2){
				// 	console.log(this.arr[index].value)
				// 	if(this.clickIndex.indexOf(index)>-1){
				// 		this.clickIndex.splice(index)
				// 	}
				// }
				//  if(this.structure.indexOf( item.value )!=-1){
				// 	this.structure.splice( this.structure.indexOf( item.value ), 1 )
				// }else{
				// 	this.structure.push(item.value )
				// }
				// console.log(this.structure)
				// console.log(this.types)
				// 	console.log(this.chooseAfterValue)
				// if(this.chooseAfterValue.length>2){
				// 	// this.chooseAfterValue.splice(-1)
				// }
			},
			hdchange(banle) {
				let type=[]
				let values=[]
				this.currIndex = banle
				if(banle == ''){
					houseIndex({
						province_id: this.province_id,
						city_id: this.city_id,
						area_id: this.area_id,
						rental_area: this.rental_area,
						rent: this.rent,
					}).then(res => {
						this.indexArr = res
					})
				}
				if(this.arr[banle[0]].type == "structure" ){
					this.structure = this.arr[banle[0]].value
				}
				if(this.arr[banle[0]].type == "floor"){
					this.floor = this.arr[banle[0]].value
				}
				if (banle.length >= 2) {
					if(this.arr[banle[1]].type == "floor"){
						this.floor = this.arr[banle[1]].value
					}
					if(this.arr[banle[1]].type == "structure"){
						this.structure = this.arr[banle[1]].value
					}
					if (this.arr[banle[0]].type == this.arr[banle[1]].type) {
						if(this.arr[banle[0]].type && this.arr[banle[1]].type == "structure"){
							this.floor = null
							console.log(11)
						}else if(this.arr[banle[0]].type && this.arr[banle[1]].type == "floor"){
							console.log(222)
							this.structure = null
						}
						banle.splice(0, 1)
						this.currIndex = banle
					}
					
				}
				
				console.log(banle)
				if(banle.length > 2){
					banle.splice(0,1)
				}
				
				// this.floor = value;
				// console.log(this.structure,this.floor)
				houseIndex({
					province_id: this.province_id,
					city_id: this.city_id,
					area_id: this.area_id,
					rental_area: this.rental_area,
					rent: this.rent,
					structure:this.structure,
					floor:this.floor,
				}).then(res => {
					this.indexArr = res
				})
				if(this.type <= 0){
					this.type =[]
					this.value =[]
				}
			},
			showMeasurePopup() {
				this.$refs["measurePopup"].show();
			},
			gotoDetails(id) {
				this.$router.push(`/workshop/details/${id}`);
			},
			selectMeasure(val) {
				if (val) {
					this.rental_area = val[1]
					this.selectMeasureArr = val[0]
					this.getData()
				} else {
					this.selectMeasureArr = []
					this.rental_area = null
					this.getData()
				}
				
				// if(val[1] == 0){
				// 	houseIndex({}).then(res => {
				// 		this.indexArr = res
				// 	})
				// }
			},
			selectmeasure(val, index) {
				if (val) {
					this.selectMeasureArr2 = val;
					this.area = val;
				} else {
					this.selectMeasureArr2 = []
				}
			},
			selectPrice(val, value) {
				if (val) {
					this.selectPriceArr = val
					this.rent = value
					this.getData()
				} else {
					this.selectPriceArr = []
					this.rent = null
					this.getData()
				}
				
				// if(value== 0){
				// 	houseIndex({}).then(res => {
				// 		this.indexArr = res
				// 	})
				// }

			},
			selectaddPrice(val, index) {
				if (val) {
					this.selectPriceArr2 = val
					this.budget = val
				} else {
					this.selectPriceArr2 = []
				}
			},
			seleProvince(list, index) {
				if (list) {
					this.selectedProvince = list
					this.province_id = index[0];
					this.city_id = index[1];
					this.area_id = index[2];
				} else {
					this.selectedProvince = []
				}
			},
			getData() {
				houseIndex({
					province_id: this.province_id,
					city_id: this.city_id,
					area_id: this.area_id,
					floor: this.floor,
					structure: this.structure,
					rental_area: this.rental_area,
					rent: this.rent
				}).then(res => {
					this.indexArr = res
				})
				getByHouse({
					type: this.type,
					value: this.value,
				}).then(res => {
					this.arr = res.tags
				})
			},
			getroadList() {
				const _self = this
				houseIndex({
					page: this.pageIndex,
					pageSize: this.pageSize
				}).then(res => {
					if (res) {
						const rows = res
						if (rows == null || rows.length === 0) {
							// 加载结束
							_self.upFinished = true
							return
						}
						if (rows.length < this.pageSize) {
							// 最后一页不足10条的情况
							_self.upFinished = true
						}
						// 处理数据
						if (_self.pageIndex === 1) {
							_self.indexArr = rows
						} else {
							_self.indexArr = _self.indexArr.concat(rows)
						}
					}
				}).catch(error => {
					this.$dialog.alert({
						message: '获取资源列表异常{' + error + '}',
						type: 'error'
					})
				}).finally(() => {
					_self.isDownLoading = false
					_self.isUpLoading = false
				})
			},
			// 上拉加载请求方法
			onLoadList() {
				this.pageIndex++;
				this.getroadList();
				this.isUpLoading = false;
			},
			saveInfo() {
				if (this.selectAreaVal.length <= 0) {
					this.$dialog.alert({
						title: "温馨提示",
						message: "请选择目标房源区域"
					})
				} else {
					findHouseAdd({
						province_id: this.province_id,
						city_id: this.city_id,
						area_id: this.area_id,
						area: this.area,
						budget: this.budget,
						remark: this.remark,
					}).then(res => {
						this.show = false
						this.$dialog.alert({
							title: "温馨提示",
							message: "提交成功"
						})

					}).catch(err =>{
						if(err){
							this.$dialog.alert({
								title: "温馨提示",
								message: "该区域暂无代理人"
							})
						}
					})
				}
			}
		},
		computed: {
			...mapGetters(["userInfo"]),
			selectedAreaCode() {
				const len = this.selectedArea.length;
				if (len > 0) {
					return this.selectedArea[len - 1].code;
				} else {
					return "100100";
				}
			},
			selectedAreaName() {
				const len = this.selectedArea.length;
				if (len > 0) {
					return this.selectedArea.map(item => item).join(" ");
				} else {
					return "";
				}
			},
			selectAreaVal() {
				const len = this.selectedProvince.length;
				if (len > 0) {
					return this.selectedProvince.map(item => item).join(" ");
				} else {
					return "";
				}
			},
			selectMeasureName() {
				const len = this.selectMeasureArr.length;
				if (len > 0) {
					return this.selectMeasureArr;
				} else {
					return "";
				}
			},
			selectMeasureName2() {
				const len = this.selectMeasureArr2.length;
				if (len > 0) {
					return this.selectMeasureArr2;
				} else {
					return "";
				}
			},
			selectPriceName() {
				const len = this.selectPriceArr.length;
				if (len > 0) {
					return this.selectPriceArr;
				} else {
					return "";
				}
			},
			selectPriceName2() {
				const len = this.selectPriceArr2.length;
				if (len > 0) {
					return this.selectPriceArr2;
				} else {
					return "";
				}
			},

		}
	}
</script>

<style lang="less" scoped>
	/* .fix{
	position: fixed;
	left: 50%;
	bottom: 10px;
	transform: translateX(-50%);
}	 */
	.van-checkbox--horizontal {
		margin-right: inherit;
	}

	.worksop {
		background-color: #f6f6f6;
	}

	.bg {
		background-color: #fff;
	}

	.celbox {
		margin: 0 15px;
		padding: 15px 0;
	}

	.sleck {
		/* height:30px;
	line-height: 30px; */
		background: rgba(237, 237, 237, 1);
		border-radius: 30px;
		text-align: center;
		background: #EDEDED;
		padding: 5px 0;
		font-size: 14px;
	}

	.yyjx_row {
		width: 100%;
		display: -webkit-box;
		justify-content: flex-start;
		align-items: center;
		white-space: nowrap;
		overflow-x: scroll;
		overflow-y: hidden;
		padding: 20px 0 0 0;
		border-top: 1px solid #d8d8d8;
	}

	.van-checkbox-group--horizontal {
		flex-wrap: nowrap;
	}

	.yyjx_row::-webkit-scrollbar {
		background-color: transparent;
		display: none;
	}

	.yyjx_item {
		padding: 2px 10px;
		border-radius: 5px;
		background-color: #EDEDED;
		margin-left: 20px;
	}

	.yyjx_item span {
		color: #666;
	}

	.active {
		background-color: #FBE8EA;
	}

	.active2 {
		color: #C80000 !important;
		font-weight: 400;
	}

	.iconfont {
		font-size: 12px;
	}

	.worksop_centent {
		margin-top: 10px;
		background: #fff;
	}

	.worksop_centent_box {
		margin: 0 15px;
		display: flex;
		align-items: center;
		justify-content: space-between;
		padding: 15px 0;
		border-bottom: 1px solid #D8D8D8;
	}

	.worksop_centent_box .van-image {
		border-radius: 10px;
	}

	.worksop_info {
		color: #333;
	}

	.worksop_info p {
		margin: 5px 0;
	}

	.worksop_info p:first-child {
		width: 200px;
		color: #333;
		font-size: 16px;
		font-weight: 400;
	}

	.worksop_info p:nth-child(2) {
		color: #999;
		font-size: 15px;
		font-weight: 400;
	}

	.worksop_info p .iconbiaodiandidian {
		font-size: 16px;
	}

	.worksop_info p:last-child {
		color: #333;
		font-size: 14px;
		font-weight: 400;
	}

	.cor {
		color: #B71730 !important;
	}

	.cor b {
		font-size: 20px !important;
	}

	.van-button--large {
		width: 50%;
		margin: 20px auto 60px;

	}

	.van-button--warning {
		color: #fff;
		width: 150px;
		height: 40px;
		line-height: 40px;
		background: linear-gradient(90deg, rgba(196, 0, 36, 1), rgba(217, 10, 32, 1));
		box-shadow: 0px 1px 2px 2px rgba(194, 21, 67, 0.3);
		border-radius: 17px;
		font-size: 16px;
		border: none;
		position: fixed;
		bottom: 0px;
		left: 50%;
		transform: translateX(-50%);
		z-index: 1;
	}

	.van-button {
		border-radius: 35px;
	}

	.wdit {
		width: 94%;
	}

	.popu_ps {
		width: 94%;
		margin: 20px auto;
		font-size: 12px;
		font-weight: 400;
		color: rgba(102, 102, 102, 1);
		opacity: 0.65;
	}

	.but {
		text-align: center;
	}

	.buts {
		width: 100%;
		height: 46px;
		line-height: 46px;
		background: rgba(153, 153, 153, 1);
		color: #fff;
		font-size: 18px;
	}

	.colors {
		background-color: #0658E0;
	}

	/* .textarea{
	width:90%;
	margin:0 auto;
	height:118px;
	background:rgba(238,238,238,1) !important;
	border-radius:5px;
	font-size: 12px !important;
} */
	.textarea textarea {
		background-color: initial;
	}

	.cf_bg {
		width: 100%;
		height: 140px;
		background: url(../../../public/img/img.png)no-repeat;
		background-size: contain;

	}

	.cf_bg p {
		display: inline-block;
		margin: 0;
		font-size: 20px;
		font-weight: bold;
		color: rgba(255, 255, 255, 1);
		position: relative;
		top: 28px;
